.homePage{
    margin: 10% auto;
    width: 60%;
    font-size: xx-large;
    font-family: 'Times New Roman', Times, serif;
    color: darkmagenta;
}

.homePageHead{
    text-align: center;
    color: red;
    font-family: 'Times New Roman', Times, serif;
    font-size: xx-large;
    font-weight: bold;
}

.postSection{
    width: 80%;
    margin: 1% auto;
}

.postBox{
    margin: 2% 0%;
}

.postItem{
    /* border: 2px solid aqua; */
    margin: 10% 0%;
    padding: 1%;
    box-shadow: 20px 20px 10px 0px rgba(99, 94, 87, 0.726);
}

.caption{
    font-size: medium;
    color: black;
    font-family: 'Courier New', Courier, monospace;
}

.likeBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: red;
    border-color: #ccc;
    color: gold;
    width: 10%;
    cursor: pointer;
    display: flex;
    justify-content: space-around;
}


.likeInp{
    visibility: hidden;
}

.likeBtnPara{
    background-color: transparent;
    color: gold;
    /* width: 40%; */
    
}

.usernameBtn{
    background-color: #E8988A;
    color: blue;
    cursor: pointer;
    border: 0px solid transparent;
    font-size:x-large ;
}


/* MEDIA QUERIES */

@media (max-width: 1024px) {

  .homePage {
    width: 95%;
    font-size: large;
    margin-top: 20%;
  }

  .homePageHead {
    font-size: large;
    padding: 0 5%;
  }

  .postSection {
    width: 100%;
    margin: 5% auto;
  }

  .postItem {
    margin: 8% 0%;
    padding: 4%;
    box-shadow: 10px 10px 8px 0px rgba(99, 94, 87, 0.4);
  }

  .likeBtn {
    width: 40%;
    font-size: small;
  }

  .caption {
    font-size: small;
    word-break: break-word;
  }

  .usernameBtn {
    font-size: medium;
    padding: 2% 4%;
  }
}
