.friendHead{
    margin: 2% 0%;
    color: blue;
    text-align: center;
    font-weight: bolder;
    font-size: x-large;
    
}

.friendList{
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 0 auto;
}

.friendBox{
    border-radius: 2%;
    border: 2px solid #ccc;
    width: 20%;
    text-align: center;
    padding: 2%;
    margin: 2% 2%;
}

.img{
    margin: 0 auto;
}

.addFriendSection{
    margin: 5% 2%;
    width: 30%;
    display: flex;
    justify-content: space-around;

}

.addFriendLegend{
    font-size: larger;
    color: brown;
    font-weight: bold;
}

.addFriendBtn,.removeBtn,.profileBtn{
    margin: 0% 2%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
}

.removeBtn,.profileBtn{
    margin: 2% 2%;
    cursor: pointer;
}


/* RESPONSIVE UI */

@media (max-width: 1024px) {

  .addFriendSection {
    margin: 5% auto;
    flex-direction: column;
    width: 90%;
    gap: 1rem;
    align-items: center;
  }

  .addFriendBtn {
    width: 60%;
  }

  .friendHead {
    font-size: large;
    margin-top: 3%;
  }

  .friendList {
    flex-direction: column;
    align-items: center;
    width: 95%;
  }

  .friendBox {
    width: 80%;
    padding: 4%;
    margin: 3% 0;
  }

  .img {
    width: 60%;
    height: auto;
  }

  .removeBtn,
  .profileBtn {
    width: 70%;
    margin: 1% auto;
    font-size: 0.9rem;
  }
}
