.loginPage{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin:5% auto;
}

.loginIcon{
    width: 20%;
}

.loginDetails{
    width: 60%;
}

.loginInput{
    width: 50%;
    font-size: medium;
    border-radius: 25px;
    padding: 1%;
}

.loginBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
}

/* RESPONSIVE UI */
@media (max-width: 1024px) {

  .loginPage {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 20%;
  }

  .loginIcon {
    width: 40%;
  }

  .loginDetails {
    width: 90%;
    text-align: center;
  }

  .loginInput {
    width: 90%;
    font-size: small;
    padding: 2%;
  }

  .loginBtn {
    width: 60%;
    font-size: small;
    padding: 2.5%;
  }
}

