.welcomePage{
    width: 80%;
    margin: 5% auto;
}

.welcomePageHead{
    text-align: center;
    color: red;
    font-family: 'Times New Roman', Times, serif;
    font-size: xx-large;
    font-weight: bold;
}

.welcomePageTagline{
    text-align: center;
    font-size: x-large;
    font-family: 'Times New Roman', Times, serif;
    color: blue;
}

.midParaContainer{
    margin: 2% 0%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.midPara{
    width:40%;
    padding: 2%;
    border: 5px solid brown;
    border-radius: 15px;
}

.introParas{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: larger;
    line-height: 1.6;
}


/* Responsive UI */

@media (max-width: 1024px) {

  .welcomePage {
    width: 95%;
    margin-top: 20%;
  }

  .welcomePageHead {
    font-size: x-large;
    padding: 0 5%;
  }

  .welcomePageTagline {
    font-size: large;
    padding: 0 5%;
  }

  .introParas {
    font-size: medium;
    padding: 0 5%;
  }

  .midParaContainer {
    flex-direction: column;
    gap: 2rem;
  }

  .midPara {
    width: 90%;
    padding: 4%;
  }
}
