.signUpPage{

    display: flex;
    margin:5% auto;
    justify-content: space-around;
    align-items: center;
}

.imgSection{
    width: 20%;
}

.formSection{
    width: 60%;
}

.formInput{
    width: 50%;
    font-size: medium;
    border-radius: 25px;
    padding:1%;
}

.signUpBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
}

/* Responsive UI */
@media (max-width: 1024px) {

  .signUpPage {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 20%;
  }

  .imgSection {
    width: 40%;
  }

  .formSection {
    width: 90%;
    text-align: center;
  }

  .formInput {
    width: 90%;
    font-size: small;
    padding: 2%;
  }

  .signUpBtn {
    width: 60%;
    font-size: small;
    padding: 2.5%;
  }
}
