.navbar {
  background-color: #F0BB78;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.5%;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.navHead {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  font-size: xx-large;
  color: #133E87;
}

/* Hamburger button */
.hamburger {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  color: #133E87;
}

/* Desktop nav links */
.navLinks {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: large;
  font-weight: bold;
}

.navItems {
  text-decoration: none;
  color: black;
  margin: 0 1rem;
}

.profileName {
  font-size: medium;
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* Responsive behavior */
@media (max-width: 1024px) {
  .hamburger {
    display: block;
  }

  .navLinks {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #F0BB78;
    flex-direction: column;
    align-items: center;
    display: none;
    padding: 1rem 0;
  }

  .navLinks.showMenu {
    display: flex;
  }

  .navItems {
    margin: 0.5rem 0;
    font-size: 1.2rem;
  }

  .profileName {
    display: none;
  }
}
