.myPostPage{
    margin: 5% auto;
    width: 60%;
}

.myPostHead{
    font-size: xx-large;
    font-family: 'Times New Roman', Times, serif;
    color: darkmagenta;
}
.postSection{
    width: 80%;
    margin: 2% auto;
    display: block;
}

.createPostSection{
    display: block;
    margin: 2% 0%;
    width: 35%;
    /* float: right; */
    display: flex;
    justify-content: flex-end;
}

.createPostSectionRight{
    border: 2px solid rgba(18, 20, 158, 0.644);
    box-shadow: 20px 20px 10px 0px rgba(18, 20, 158, 0.644);
}

.createPostBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 3%;
    cursor: pointer;
}

.createPostHead{
    font-size: larger;
    color: red;
}

.postBox{
    margin: 4% 0%;
}

.idInp{
    visibility: hidden;
}

.caption{
    font-size: medium;
    color: black;
    font-family: 'Courier New', Courier, monospace;
}

.removeBtn{
    margin: 2% auto;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 1%;
    width: 20%;
    cursor: pointer;
}

.likes{
    font-size: large;
    color: red;
    font-family: 'Courier New', Courier, monospace;
}

.inputSection{
    margin: 2% 0%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: medium;
    color: black;
}

.displayPostSection{
    margin: 5% 0%;
}


/* RESPONSIVE UI */
@media (max-width: 1024px) {

  .myPostPage {
    width: 95%;
    margin-top: 20%;
  }

  .createPostSection {
    width: 100%;
    justify-content: center;
    margin: 5% 0%;
  }

  .createPostSectionRight {
    width: 90%;
    padding: 5%;
    box-shadow: 10px 10px 8px 0px rgba(18, 20, 158, 0.3);
  }

  .createPostHead {
    font-size: large;
    text-align: center;
  }

  .inputSection {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .createPostBtn {
    width: 100%;
    font-size: small;
    padding: 2%;
  }

  .myPostHead {
    font-size: x-large;
    text-align: center;
  }

  .postSection {
    width: 100%;
    margin: 5% auto;
  }

  .postBox {
    margin: 6% 0%;
    padding: 3%;
  }

  .caption {
    font-size: small;
    word-break: break-word;
  }

  .likes {
    font-size: medium;
  }

  .removeBtn {
    width: 80%;
    font-size: small;
    padding: 2%;
  }
}
